<template>
  <div>
    <v-header />
    <div class="deatails-header">
      <div class="web">
        <Row>
          <Col span="8">
            <div class="big-img">
              <img :src="proBigImg" alt />
            </div>
            <ul class="small-img clearfix">
              <li v-for="(item, index) in proImg" :key="index" @click="proBigImg = item">
                <img :src="item" alt />
              </li>
            </ul>
          </Col>
          <Col span="12" offset="4">
            <h3 class="pro-name">{{proName}}</h3>
            <Row class="price-box" v-if="getUserInfo.token">
              <Col span="6" class="tip">{{priceTip}}</Col>
              <Col span="18">
                <ul class="price-list">
                  <li class="clearfix" v-for="(val, key, index) in priceList" :key="index">
                    <span class="num">{{key}}</span>
                    <span class="price">
                      <em>{{val}}</em>
                    </span>
                  </li>
                </ul>
              </Col>
            </Row>
            <div class="login-tip" v-else>
              请
              <router-link to="/web/login" replace>登录</router-link>后查看商品价格
            </div>
            <Row class="pl20 buy-num">
              <Col span="2" class="num">数量</Col>
              <Col span="6">
                <InputNumber :max="stock" :min="1" v-model="buyNum"></InputNumber>
              </Col>
            </Row>
            <div class="pl20">
              <Button icon="md-cart" type="primary" @click="addGood">加入购物车</Button>
            </div>
          </Col>
        </Row>
      </div>
    </div>

    <div class="detail-content web">
      <Row>
        <Col span="8" v-for="(val, key, index) in detailSku" :key="index">
          <span class="key">{{key}}</span>
          <span class="val">{{val}}</span>
        </Col>
      </Row>
    </div>

    <div class="detail-report web" v-if="reprotList.length">
      <p class="detail-title">
        <span>产品检测报告</span>
      </p>
      <ul class="report-list clearfix">
        <li v-for="(item, index) in reprotList" :key="index">
          <img :src="item" alt />
        </li>
      </ul>
    </div>

    <div class="detail-imgs web" v-if="detailImgs.length">
      <p class="detail-title">
        <span>商品信息</span>
      </p>
      <ul class="detail-list">
        <li v-for="(item, index) in detailImgs" :key="index">
          <img :src="item" alt />
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
export default {
  data() {
    return {
      proName: "",
      proBigImg: "",
      goodSn: this.$route.query.goodSn, //商品编码
      proImg: [],
      stock: 0, //库存
      buyNum: 1, //购买数量
      priceTip: "", //价格提示
      priceList: {}, //价格列表
      detailSku: {},
      reprotList: [], //检测报告列表
      detailImgs: [] //详情内容
    };
  },
  computed: {
    ...mapGetters(["getUserInfo", "getShopingCart"])
  },
  created() {
    this.getDetails();
  },
  methods: {
    ...mapActions(["setShopingCart"]),
    // 获取详情
    getDetails() {
      this.$api
        .proDetails({
          goodSn: this.goodSn
        })
        .then(res => {
          "";
          let resData = res.data.data;
          this.proName = resData.syGoods.goodsName;
          this.proImg = resData.syGoods.goodsSubimg
            ? resData.syGoods.goodsSubimg.split(",")
            : [];
          this.proBigImg = this.proImg[0] || resData.syGoods.goodsImg;
          this.stock = resData.syGoodsAttr.goodsTotalCountSurplus;
          this.priceTip = resData.syGoodsAttr.priceSku.title;
          this.priceList = JSON.parse(resData.syGoodsAttr.priceSku.value);
          this.detailSku = JSON.parse(resData.syGoodsAttr.goodsSku);
          this.reprotList = resData.syGoodsAttr.goodsDetailReport
            ? resData.syGoodsAttr.goodsDetailReport.split(",")
            : [];
          this.detailImgs = resData.syGoodsAttr.goodsDetailImgs
            ? resData.syGoodsAttr.goodsDetailImgs.split(",")
            : [];
        })
        .catch(err => console.log(err));
    },
    // 加入购物车
    addGood() {
      let sku_str = JSON.stringify(this.detailSku);
      this.$api
        .addGood({
          goodSn: this.goodSn,
          num: this.buyNum,
          sku: sku_str
        })
        .then(res => {
          //   this.$Notice.info({
          //     title: "加入成功"
          //   });
          let isNewGood = true;
          this.getShopingCart.filter(item => {
            if (item.goodsSn == this.goodSn) {
              item.num += this.buyNum;
              isNewGood = false;
            }
          });
          if (isNewGood) {
            this.getShopingCart.push({
              num: this.buyNum
            });
          }
          this.setShopingCart(this.getShopingCart);
          this.$router.push("/web/addToCart");
        })
        .catch(err => console.log(err));
    }
  }
};
</script>
<style lang="less" scoped>
@import "~@/less/mixins.less";
.deatails-header {
  border-top: 36px solid #ddd;
  padding: 40px 0 50px;
  font-size: 18px;
  .fl {
    width: 400px;
  }
  .fr {
    width: 604px;
  }
}
.big-img {
  width: 400px;
  height: 400px;
  margin-bottom: 20px;
  background-color: @borderColor;
  img {
    .blockImg();
  }
}
.small-img {
  margin: 0 -13px;
  li {
    float: left;
    width: 25%;
    padding: 0 13px;
    cursor: pointer;
    img {
      .blockImg();
      background-color: @borderColor;
    }
  }
}
.pro-name {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 25px;
}
.login-tip {
  width: 100%;
  height: 280px;
  padding: 50px;
  text-align: center;
  font-size: 18px;
  background-color: @disableColor;
  a {
    color: @themeColor;
    &:hover {
      text-decoration: underline;
    }
  }
}
.price-box {
  height: 280px;
  padding: 20px;
  background-color: @disableColor;

  .tip {
    font-size: 18px;
    margin-top: 10px;
  }
}
.price-list {
  li {
    font-size: 18px;
    padding: 5px 25px;
    border-bottom: 1px solid @borderColor;
    .num {
      float: left;
    }
    .price {
      float: right;
      em {
        color: @themeColor;
        font-style: normal;
      }
    }
  }
}
.buy-num {
  margin: 20px 0;
  .num {
    line-height: 32px;
  }
}
.detail-content {
  font-size: 14px;
  line-height: 2;
  border-top: 1px solid @borderColor;
  border-bottom: 1px solid @borderColor;
  padding: 38px 0 38px 120px;
  .key {
    display: inline-block;
    vertical-align: middle;
    width: 130px;
    color: #999;
  }
  .val {
    display: inline-block;
    vertical-align: middle;
    color: #000;
  }
}
.detail-title {
  border-bottom: 1px solid @themeColor;
  margin: 50px 0 30px;
  span {
    display: inline-block;
    padding: 18px 0;
    width: 200px;
    text-align: center;
    font-size: 24px;
    color: #fff;
    background-color: @themeColor;
  }
}
.report-list {
  li {
    float: left;
    width: 180px;
    margin-right: 30px;
    img {
      .blockImg();
    }
  }
}
.detail-list {
  padding-bottom: 100px;
  img {
    .blockImg();
  }
}
.pl20 {
  padding-left: 20px;
}
.ivu-btn {
  font-size: 18px;
}
</style>


